<template lang="html">
  <iframe :src="url"></iframe>
</template>

<script>
var rightFieldEl = document.querySelector('.right-field')
export default {
  props: {
    url: String
  },
  mounted () {
    rightFieldEl.style.overflow = 'hidden'
  },
  destroyed () {
    rightFieldEl.style.overflow = 'auto'
  }
}
</script>

<style lang="css">
  iframe{
    border: 0;
    outline: 0;
    height: 100%;
    width: 100%;
  }
</style>



