<template>
  <div id="app">
    <textarea class="cp-console" v-model='console'></textarea>
    <div class="cp-form">
      <form-item :label="item.label" :name="item.name" :key="item.name" :tip="item.tip" v-for="item in formItems">
        <CpInput v-if="item.type == 'input'" v-model="params[item.name]" />
        <CpSelect v-if="item.type == 'select'" :options="item.options" v-model="params[item.name]" />
      </form-item>
    </div>
    <canvas id="qrcode" width="500" height="500"></canvas>
  </div>
</template>

<script>
import base64url from 'base64url'
import hexRgb from 'hex-rgb'
import FormItem from '@/components/common/FormItem'
import CpInput from '@/components/common/CpInput'
import CpSelect from '@/components/common/CpSelect'
import QRCode from 'qrcode'
export default {
  name: 'app',
  data () {
    return {
      formItems: [
        {
          type: 'select',
          label: '应用平台',
          name: 'turnTo',
          options: [
            { k: '浏览器', v: 'b' },
            { k: '应用商店', v: 'a' },
            { k: '主题美化', v: 't' }
          ],
          default: 'a'
        },
        {
          type: 'select',
          label: '安装完成打开方式',
          name: 'openType',
          options: [
            { k: '手动打开', v: '0' },
            { k: '直接打开', v: '1' }
          ],
          default: '0'
        },
        { type: 'input', label: '中转页url', name: 'gameUrl', tip: 'cp自定义页面，不填则默认使用简单页面' },
        { type: 'input', label: '页面标题', name: 'title' },
        { type: 'input', label: '埋点（页面来源）', name: 'business', tip: '<a target="_blank" href="http://appff.meizu.com/front/wiki/#!specifications/track.md#flyme_business_source">参考埋点文档(flyme_business_source字段)</a>' }
      ],
      params: {},
      plusParams: []
    }
  },
  computed: {
    console () {
      let result = 'https://i3.mzres.com/resources/appStore/cp/p-mul.html?'
      let params = []
      let gameUrl = this.params.gameUrl && base64url.encode(this.params.gameUrl)
      let scheme = this.params.scheme && base64url.encode(this.params.scheme)
      for (let k in this.params) {
        if (this.params.hasOwnProperty(k) && ['scheme', 'gameUrl'].indexOf(k) === -1) {
          let value = this.params[k]
          if (~k.indexOf('Color') && ~value.indexOf('#')) {
            value = `rgb(${hexRgb(value).join(',')})`
          }
          if (value) {
            params.push(`${k}=${value}`)
          }
        }
      }
      if (gameUrl) {
        params.push('gameUrl=' + gameUrl)
      }
      if (scheme) {
        params.push('scheme=' + scheme)
      }
      if (this.plusParams.length > 0) {
        this.plusParams.forEach(p => {
          if (p.key && p.value) {
            params.push(`${p.key}=${p.value}`)
          }
        })
      }
      result += params.join('&')
      return result
    }
  },
  watch: {
    console (val) {
      var canvas = document.getElementById('qrcode')
      QRCode.toCanvas(canvas, val, function (error) {
        if (error) {
          console.error(error)
        } else {
          console.log('success!')
        }
      })
    }
  },
  methods: {
    addParam () {
      this.plusParams.push({
        key: '',
        value: ''
      })
    },
    removeParam (e) {
      this.plusParams.splice(e, 1)
    }
  },
  created () {
    this.formItems.map(item => {
      this.$set(this.params, item.name, item.default || '')
    })
  },
  components: {
    FormItem, CpInput, CpSelect
  }
}
</script>

<style>
input,select,textarea {
  outline: none;
}
a {
  cursor: pointer;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
  padding: 10px;
  width: 100%;
}
.cp-desc {
  font-weight: bold;
  font-style: italic;
  color: #555;
}
.cp-btn {
  font-size: 16px;
  font-style: normal;
  padding: 2px 10px;
  border-radius: 3px;
  color: #fff;
  background-color: #42b983;
  float: right;
}
.cp-btn.warn {
  background-color: #f60;
}
.cp-input_pluskey {
  width: 200px !important;
}
</style>



