<template>
  <el-menu
    :default-active='activeUrl'
    :router='true'
    :unique-opened='true'
    class="el-menu-vertical"
    @open="handleOpen"
    @close="handleClose">
    <template v-for='(item, index) in list'>
      <template v-if='item.group'>
        <el-submenu :index="index+''" v-bind:key='index'>
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span>{{item.title}}</span>
          </template>
          <el-menu-item-group>
            <el-menu-item v-for='(one, index) in item.group' :index="one.url" v-bind:key='index'>{{one.name}}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </template>
      <template v-if='!item.group'>
        <el-menu-item :index="item.url" v-bind:key='index'>
          <i class="el-icon-setting"></i>
          <span slot="title">{{item.name}}</span>
        </el-menu-item>
      </template>
    </template>
  </el-menu>
</template>

<script>
export default {
  data: function () {
    return {
      activeUrl: '',
      list: [
        {
          url: './cpcustomization',
          name: 'cp定制化接入文档'
        },
        {
          title: '多应用配置',
          group: [
            {
              url: './mulreadme',
              name: '说明文档'
            }, {
              url: './mulcodeconfig',
              name: '注入代码获取'
            }, {
              url: './mulurlconfig',
              name: '页面路径配置'
            }
          ]
        },
        {
          title: '导航页配置',
          group: [
            {
              url: './navreadme',
              name: '说明文档'
            }, {
              url: './navcodeconfig',
              name: '注入代码获取'
            }
          ]
        },
        {
          title: '其他工具',
          group: [
            {
              url: './devicestest',
              name: '多机型适配'
            }, {
              url: './schemecheck',
              name: 'scheme有效性校验'
            }
          ]
        },
        {
          title: '旧版本单应用配置',
          group: [
            {
              url: './singlereadme',
              name: '说明文档'
            }, {
              url: './singletest',
              name: '流程模拟测试'
            }, {
              url: './singleurlconfig',
              name: '页面路径配置'
            }
          ]
        }
      ]
    }
  },
  methods: {
    handleOpen (key, keyPath) {

    },
    handleClose (key, keyPath) {

    }
  },
  mounted () {
    this.activeUrl = `.${location.hash.replace('#', '')}`
  }
}
</script>

<style lang="css">
  .el-menu-vertical{
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
  }
</style>



