<template lang="html">
  <input type="text" class="cp-form_input" :value="value" @input="$emit('input', $event.target.value)" :placeholder='placeholder'/>
</template>

<script>
export default {
  props: {
    value: String,
    placeholder: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="css">
</style>



