<template>
  <el-row class="row-container">
    <el-col :span="4" class="left-nav">
      <LeftNav/>
    </el-col>
    <el-col :span="20" class='right-field'>
      <router-view/>
    </el-col>
  </el-row>
</template>

<script>
import LeftNav from '@/components/LeftNav'
export default {
  name: 'App',
  components: {
    LeftNav
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body, .row-container, .left-nav, .right-field {
  height: 100%;
  margin: 0;
  padding: 0;
}
.right-field {
  overflow-y: auto;
}
</style>



